import React from "react";

import GradientButton from "../../controls/GradientButton";

import "./Introduction.scss";

const Introduction = () => {
  return (
    <div className="introduction-container">
      <img
        className="introduction-balloon-left"
        src="assets/balloon-left-1.png"
        alt="balloon"
      ></img>
      <img
        className="introduction-balloon-right"
        src="assets/balloon-right-1.png"
        alt="balloon"
      ></img>
      <div className="row">
        <div className="col-xl-8 introduction-caption">
          <span className="introduction-badge">CNFT GENIE</span>
          <h1 className="introduction-title">
            NFT<br></br>MARKETPLACE
          </h1>
          <div className="introduction-description">
            The Cardano Non-Fungible Token exchange with Smart Contracts and
            Minting
          </div>
          <div className="introduction-button-group">
            <GradientButton
              text="Browse"
              style={{ backgroundColor: "#19535e", marginRight: "30px" }}
            ></GradientButton>
            <GradientButton
              text="Create"
              style={{ backgroundColor: "#19535e" }}
            ></GradientButton>
          </div>
        </div>
        <div className="col-xl-4 introduction-price">
          <div className="introduction-price-pane">
            <div className="introduction-price-title">CARDANO</div>
            <div className="introduction-price-unit">ADA</div>
            <div className="introduction-price-dollar-title">Price</div>
            <div className="introduction-price-dollar-value">$1.54</div>
            <div className="introduction-price-change-pane">
              <div className="introduction-price-change-title">
                24 HR % CHANGE
              </div>
              <div className="introduction-price-change-value">-1.21%</div>
            </div>
            <div className="introduction-price-capital-pane">
              <div className="introduction-price-capital-title">
                MARKET CAPITAL
              </div>
              <div className="introduction-price-capital-value">$49.47B</div>
            </div>
            <div className="introduction-price-circle"></div>
            <img
              className="introduction-price-pane-background"
              src="assets/price_pane_background.png"
              alt="price pane background"
            ></img>
            <img
              className="introduction-price-pane-stripe"
              src="assets/price_pane_stripe.png"
              alt="price pane stripe"
            ></img>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Introduction;
