import React from "react";
import "./TransparentCard.scss";

const TransparentCard = (props) => {
  return (
    <div className="transparent-card" style={props.style}>
      <img
        className="transparent-card-icon"
        src="assets/card_creator.png"
        alt="card creator"
      ></img>
      <div className="transparent-card-text">{props.text}</div>

      {!props.isLast ? (
        <i className="transparent-card-arrow fa fa-long-arrow-right"></i>
      ) : (
        <></>
      )}
    </div>
  );
};

export default TransparentCard;
