import React from "react";
import GradientButton from "../../controls/GradientButton";
import "./AdaInfo.scss";
const AdaInfo = () => {
  return (
    <div className="ada-info-container">
      <div className="ada-info-text-background">CNFT GENIE</div>
      <img
        className="ada-info-balloon-left"
        src="assets/balloon-left-1.png"
        alt="balloon"
      ></img>
      <img
        className="ada-info-balloon-right"
        src="assets/balloon-right-1.png"
        alt="balloon"
      ></img>
      <div className="ada-info-left">
        <div className="ada-info-title">
          CREATE, SELL<br></br> OR COLLECT<br></br> NFTS AT NFTPRO
        </div>
        <div className="ada-info-button-group">
          <GradientButton text="Discover More"></GradientButton>
          <GradientButton text="Collect NFT"></GradientButton>
        </div>
      </div>
      <div className="ada-info-right">
        <img src="assets/ada.png" alt="ada"></img>
      </div>
    </div>
  );
};
export default AdaInfo;
