import React from "react";
import "./GradientChooser.scss";

const GradientChooser = (props) => {
  return (
    <div
      className={
        props.isSelected === true
          ? "gradient-chooser-selected gradient-chooser"
          : "gradient-chooser"
      }
      style={props.style}
    >
      <span>{props.text}</span>
    </div>
  );
};

export default GradientChooser;
