import React, { Fragment } from "react";
import { Container } from "react-bootstrap";
import "./Footer.scss";
const Footer = () => {
  return (
    <Container style={{ maxWidth: "90%" }}>
      <div class="footer-container">
        <div class="footer-menu">
          <div class="footer-about">
            <div class="footer-about-title">
              <img src="assets/logo_footer.png" alt="logo"></img>
              <div class="footer-about-title-text">CNFT GENIE</div>
            </div>
            <div class="footer-about-description">
              CNFTGENIE.IO is a Cardano Non-Fungible Token marketplace. We are
              based in Austin,TX, a new silicone valley. While we are not the
              first market place based on the Cardano blockchain, we are the
              best and can officially say that we are the first to offer both an
              exchange and minting on the same platform. Additionally, we offer
              an auction feature as another way to sell your NFTs. <br></br>If
              you need any help, then please Contact Us, and we will be happy to
              assist you!
            </div>
          </div>
          <div class="footer-marketplace">
            <div class="footer-title">MARKET PLACE</div>
            <div class="footer-link-group">
              <div class="footer-link">Art</div>
              <div class="footer-link">All NFTs</div>
              <div class="footer-link">Music</div>
              <div class="footer-link">Trending Cards</div>
            </div>
          </div>
          <div class="footer-account">
            <div class="footer-title">MY ACCOUNT</div>
            <div class="footer-link-group">
              <div class="footer-link">Authors</div>
              <div class="footer-link">Collection</div>
              <div class="footer-link">Seller Profile</div>
              <div class="footer-link">Create Collection</div>
            </div>
          </div>
          <div class="footer-resources">
            <div class="footer-title">RESOURCES</div>
            <div class="footer-link-group">
              <div class="footer-link">Helps Support</div>
              <div class="footer-link">Live Auctions</div>
              <div class="footer-link">Item Details</div>
              <div class="footer-link">Activity</div>
            </div>
          </div>
          <div class="footer-company">
            <div class="footer-title">COMPANY</div>
            <div class="footer-link-group">
              <div class="footer-link">Buy</div>
              <div class="footer-link">Sell</div>
              <div class="footer-link">Auction</div>
              <div class="footer-link">Mint</div>
            </div>
          </div>
        </div>
        <div class="footer-copyright">
          2022 copyright CNFTGENIE, LLC, all rights reserved.
        </div>
      </div>
    </Container>
  );
};

export default Footer;
