import React from "react";

import GradientChooser from "../../controls/GradientChooser";
import "./TokenGallery.scss";
import TokenCard from "../../controls/TokenCard";

const TokenGallery = () => {
  return (
    <div className="token-gallery-container">
      <img
        className="token-gallery-background"
        src="assets/token_gallery_background.png"
        alt="balloon"
      ></img>
      <div className="token-gallery-text-background">FEATURED TOKENS</div>
      <div className="token-header">
        <div className="token-gallery-title">FEATURED TOKENS</div>
        <div className="token-gallery-chooser-group">
          <GradientChooser text="All" isSelected={true}></GradientChooser>
          <GradientChooser
            text="Virtual Worlds"
            isSelected={false}
          ></GradientChooser>
          <GradientChooser
            text="Collectibles"
            isSelected={false}
          ></GradientChooser>
          <GradientChooser text="Art" isSelected={false}></GradientChooser>
        </div>
      </div>

      <div className="token-gallery-card-group">
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
        <TokenCard></TokenCard>
      </div>
    </div>
  );
};

export default TokenGallery;
