import React from "react";

import TransparentCard from "../../controls/TransparentCard";

import "./Royalties.scss";

const Royalties = () => {
  return (
    <div className="royalties-container">
      <div className="royalties-title">OUR ROYALTIES</div>
      <div className="royalties-description">
        Our royalties feature gives you a percentage of the sale price each time
        your NFT creation is sold on a marketplace. Your royalty payments are
        perpetual and are executed by smart contracts automatically each time
        your artwork sells
      </div>
      <div className="royalties-card-group">
        <TransparentCard text="CREATOR"></TransparentCard>
        <TransparentCard text="BUYER"></TransparentCard>
        <TransparentCard text="BUYER SELLS"></TransparentCard>
        <TransparentCard text="YOU GET PAID" isLast="true"></TransparentCard>
      </div>
      <div className="royalties-background">
        {true ? (
          <img
            src="assets/royalties_background.png"
            alt="royalties background"
          ></img>
        ) : (
          <></>
        )}
      </div>
    </div>
  );
};

export default Royalties;
