import React from "react";
import "./TokenCard.scss";

const TokenCard = (props) => {
  return (
    <div className="token-card">
      <div className="token-card-image">
        <img
          className="token-card-image-background"
          src="assets/image_crane.png"
          alt="crane"
        ></img>

        <div className="token-card-image-info">
          <div className="token-card-image-info-avatar">
            <img src="assets/avatar1.png" alt="avatar1"></img>
          </div>
          <div className="token-card-image-info-text">Created by @Julian</div>
        </div>
      </div>
      <div class="token-card-bottom">
        <div class="token-card-bottom-first">
          <div className="token-card-title">INDUSTRIAL REVOLUTION</div>
          <div className="token-card-price">
            <div className="token-card-price-value-unit">
              <span className="token-card-price-value">1500</span>
              <span className="token-card-price-unit">&nbsp;ADA</span>
            </div>
          </div>
        </div>
        <div class="token-card-bottom-second">
          <div className="token-card-favourite-group">
            <div className="token-card-favourite">
              <img src="assets/heart.png" alt="heart"></img>
              <div className="token-card-favourite-value">10+</div>
            </div>
            <div className="token-card-favourite-description">
              People Placed Bid
            </div>
          </div>
          <div className="token-card-button">
            <div className="token-card-button-text">&gt;</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TokenCard;
