import React from "react";
import { Navbar, Container, Nav, Form, FormControl } from "react-bootstrap";

import "./Header.scss";
import LoginButton from "../controls/LoginButton";
import SignUpButton from "../controls/SignUpButton";
const Header = () => {
  return (
    <Navbar bg="transparent" expand="lg">
      <Container className="header-container">
        <Navbar.Brand className="header-logo" href="#home">
          <img src="assets/logo.png" alt="logo"></img>
          <span>CNFT GENIE</span>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Form className="d-flex">
            <FormControl
              type="search"
              placeholder="Search..."
              className="me-2 header-search"
              aria-label="Search..."
            />
          </Form>
          <Nav className="header-menu">
            <Nav.Link className="header-menu-link" href="#home">
              Home
            </Nav.Link>
            <Nav.Link className="header-menu-link" href="#buy">
              Buy
            </Nav.Link>
            <Nav.Link className="header-menu-link" href="#sell">
              Sell
            </Nav.Link>
            <Nav.Link className="header-menu-link" href="#auction">
              Auction
            </Nav.Link>
            <Nav.Link className="header-menu-link" href="#mint">
              Mint
            </Nav.Link>
          </Nav>
          <div className="header-button-group">
            <SignUpButton text="Sign Up"></SignUpButton>
            <LoginButton text="Login"></LoginButton>
          </div>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
